@import './reset.scss';

$메인컬러 : #ff0000;

.wow-enter {//애니메이션 시잘 때 적용
    opacity: 0;
}

.wow-enter-active {//애니메이션 동작 때 사용
    opacity: 1;
    transition: all 500ms;
}

.red {
    color: $메인컬러;
}

// div.container h4 {
//     color: blue;
// }

div.container {
    h4 {
        color: blue;
    }
}
@mixin 함수() {
    background-color: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}
.my-alert {
    @include 함수()
}
.my-alert2 {
    @extend .my-alert;
    background: yellow;
}
.my-alert p {
    margin-bottom: 0;
}

